import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Brainstorming from '../views/Brainstorming.vue'
import Members from '../views/Members.vue'
import Interview from '../views/Interview.vue'
import Personnas from '../views/Personnas.vue'
import IP from '../views/IP.vue'
import videoPrototype from '../views/videoPrototype.vue'
Vue.use(VueRouter)

const routes = [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
        path: '/members',
        name: 'Members',
        component: Members
    },
    {
        path: '/ip',
        name: 'ip',
        component: IP
    },
    {
        path: '/personnas',
        name: 'Personnas',
        component: Personnas
    },
    {
        path: '/interview',
        name: 'Interview',
        component: Interview
    },
    {
        path: '/interview/:arg',
        name:"interview_p",
        component:Interview
    },
    {
      path: '/Brainstorming',
      name: 'Brainstorming',
      component: Brainstorming
    },
    {
      path: '/final_proto',
      name: 'video',
      component: videoPrototype
    }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
