                        q:" Tu me dis que ça fait 2 mois que tu n’as pas fait de sport est-ce que tu trouves ça difficile d'être régulière ?",
                        a:`Maintenant c’est à la maison et de la marche à pied (tapis de course). Pour
                            reprendre c’est un peu difficile. Après le fait d’être malade souvent (estomac, cancer
                            de la thyroïde), avec des petits soucis de santé par ci par là ça rend difficile le fait
                            d’être régulière, dès que je suis malade je dois arrêter pendant de longues périodes.
                            Et même quand je fais du sport parfois je me sens mal et je suis obligée d’arrêter.`
                    }
                ],
                "lyna":[
                    {
                        q:"Quel est ton rapport au sport dans ton quotidien ?",
                        a:"J’aime bien faire du sport mais je n’en fais pas si souvent que ça. Pas forcément parce j’ai pas le temps mais juste comme ça, même si j’aime bien, je ne suis pas très douée en sport."
                    },
                    {
                        q:"Pourquoi tu n’en fais pas si souvent ?",
                        a:"Je suis trop fainéante."
                    },
                    {
                        q:"Qu’est ce qui te motiverais à aller faire du sport ?",
                        a:"Avoir moins de devoirs."
                    },
                    {
                        q:"Donc c’est un problème de temps ?",
                        a:"Et parce que quand j’ai du temps libre, je préfère aller faire du sport. Je préfère faire des choses que j’aime plus comme regarder des séries par exemple."
                    },
                    {
                        q:"Est ce que tu préfères faire du sport seule ou en équipe ?",
                        a:"Seule."
                    },
                    {
                        q:"Quand tu dis seule, c’est toute seule ou des sports solitaires ?",
                        a:"Les deux, parce que j’aime pas qu’on me regarde quand je fais du sport. Et quand je pratique des sports en équipe, j’ai l’impression d’être toujours le maillon faible donc ça ne me plaît pas."
                    },
                    {
                        q:"Qu’est ce qui te permettrait d’être plus douée en sport ?",
                        a:"Probablement d’en faire plus, mais comme je ne suis pas douée, j’aime pas en faire devant les autres donc je ne m'améliore pas. Je suis toujours gênée d’en faire devant les autres."
                    },
                    {
                        q:"Comment s’est passé ta dernière séance de sport ?",
                        a:"C’était mon bac de gym et ça s’est bien passé. Au début, on s’est entraîné chacun de notre côté et après on est passé chacun notre tour devant les profs."
                    },
                    {
                        q:"Quand tu vas faire du sport c’est où mis à part l’école ?",
                        a:"Dans ma chambre, très rarement de temps en temps je vais courir et je fais de la boxe mais j’y vais plus à cause du Pass Vaccinal et du bac."
                    },
                    {
                        q:"S’il y avait pas le Covid tu aurais continué la boxe ?",
                        a:"Non parce que à chaque fois j’ai des devoirs."
                    },
                    {
                        q:"Donc le problème, c’est de trouver le temps de faire du sport sans être gêné par le regard des autres c’est ça ?",
                        a:"Oui."
                    },
                    {
                        q:"Est ce qu’il t'est déjà arrivé de faire face à ce problème et de trouver une solution pour pallier à ces problèmes ?",
                        a:`Je sais qu’il y avait des moments l’année dernière où je faisais du sport en regardant des séries et pour le regard des autres,
                        par exemple pour la boxe, à chaque fois je me force à y aller parce que je suis trop mal à l’aise, je fais une heure de boxe mais je me sens pas bien.
                        J’ai envie d’être forte en boxe mais je suis trop gênée quand je suis entourée. Du coup ça
                        aide pas parce que même quand j’ai le temps d’y aller quand je peux quand même y aller
                        avec mes devoirs, je préfère me donner l’excuse des devoirs pour ne pas y aller pour ne pas
                        passer une heure à être gênée.`
                    },
                    {
                        q:"Donc c’est plus une question de gêne que de temps en vrai ? Pourquoi cette gêne avec les gens ?",
                        a:"Je suis gênée parce que je n’ai pas un bon niveau donc ça me gêne j’ai l’impression d’être ridicule."
                    },
                    {
                        q:"Les autres sont doués ?",
                        a:"Pas tous mais quelques-uns oui."
                    },
                    {
                        q:"Donc si tu étais uniquement avec des gens doués ça te rassurerait sur ton niveau ?",
                        a:"Oui mais d’un autre côté tu ne peux pas vraiment progresser si tu es qu'avec des gens qui ont un bas niveau."
                    },
                    {
                        q:"Est ce qu’il y a déjà eu une fois où tu es allée à la boxe et où tu as surpassé le regard des autres ?",
                        a:"Il y a toujours des petits moments où j’oublie un peu mais c’est juste temporaire. Je ne pense pas que ça peut se supprimer d’un coup."
                    },
                    {
                        q:"Quand tu cours, tu cours ou ?",
                        a:"Dans les rues en face de chez moi, ou dans un parc pas loin."
                    },
                    {
                        q:"Comment tu choisis les endroits ou tu cours ?",
                        a:`Je cours là où il y a la place de courir, où c’est près de chez moi et dans les rues où il y a
                            personne. C’est important pour moi qu’il y ait pas beaucoup de monde dans la rue. Quand je
                            cours et qu’il y a quelqu’un qui passe dans le sens inverse sur le trottoir, je fais comme si
                            tout allait bien, je me redresse et je contrôle ma respiration et dès que la personne passe je
                            me lâche complètement.`
                    },
                    {
                        q:"Est ce que tu cours toute seule ou accompagné ?",
                        a:`Souvent seule, sinon accompagnée de ma mère et ma soeur.`
                    },
                    {
                        q:"Est ce qu’il y a quelque chose que tu aimerais améliorer dans ton expérience ?",
                        a:`Quand je cours j’écoute de la musique et mon téléphone rebondit dans ma poche.
                            J’aimerais bien qu’il soit attaché quelque part.`
                    },
                    {
                        q:"Qu’est ce qui te motive à aller courir ?",
                        a:"Avoir plus d’endurance."
                    },
                    {
                        q:"Est ce que tu utilises des outils, des applications ou des cartes pour courir ?",
                        a:"Non."
                    }
                ]
            }
        }
    },
    methods:{
        linkChanged(){
            if(this.link!==this.$route.params.arg){
                this.link = this.$route.params.arg
            }
        },
        getLink(){
            switch(this.link){
                case "lorenzo":
                    return "https://github.com/GroupeIhm2022/projet/blob/main/interview/sports/interview_de_lorenzo.txt"
                case "fares":
                    return "https://github.com/GroupeIhm2022/projet/blob/main/interview/sports/interview_de_fares.txt";
                case "azouaou":
                    return "https://github.com/GroupeIhm2022/projet/blob/main/interview/sports/interview_de_elias.md";
                case "david":
                    return "https://github.com/GroupeIhm2022/projet/blob/main/interview/sports/interview_de_david.pdf";
                case "lyna":
                    return "https://github.com/GroupeIhm2022/projet/blob/main/interview/sports/interview_de_lyna.pdf";
                default:
                    return "#"
            }
        }
    },
    filters: {
        capitalize(value) {
            if (!value) return ''
            value = value.toString()
            return value.charAt(0).toUpperCase() + value.slice(1)
        }
    },
    mounted(){
        this.link = this.$route.params.arg;
    }
}
